/* All Responsive design */

@media only screen and (max-width: 700px) {
  .media-nav-items {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    width: auto;
    border-radius: 20px;
    right: 0;
    margin-right: 5px;
    margin-top: -420px;
    position: absolute;
    z-index: 3;
  }

  .media-nav-link {
    padding: 10px;
  }
  .media-nav-border1 {
    border-top-right-radius: 20px;
    box-shadow: 0 0 6px 1px #20c20e inset;
  }
  .media-nav-border2 {
    box-shadow: 0 0 6px 1px #20c20e inset;
  }
  .media-nav-border3 {
    box-shadow: 0 0 6px 1px #20c20e inset;
  }
  .media-nav-border4 {
    box-shadow: 0 0 6px 1px #20c20e inset;
  }
  .media-nav-border5 {
    border: 1px solid #20c20e;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    box-shadow: 0 0 6px 1px #20c20e inset;
  }
  .media-nav-border6 {
    box-shadow: 0 0 6px 1px #20c20e inset;
  }
}
