.tech-stack {
  margin: 25px 40px 25px 0px;
}

.tech-stack-header {
  display: flex;
  align-items: baseline;
  justify-content: left;
  text-align: left;
  width: 100%;
  border: 1px solid #20c20e;
  border-left: 0px;
  border-radius: 20px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  box-shadow: 0 0 10px 1px #20c20e;
  padding-top: 30px;
  padding-bottom: 10px;
  padding-left: 20px;
}

.tech-stack-header h2 {
  font-size: 26px;
  font-weight: bolder;
  margin-left: 0px;
}

.tech-stack-header p {
  font-size: 9px;
  margin-left: 5px;
}

/* tech stack body */
.tech-stack-body {
  width: 100%;
  height: 500px;
  border: 1px solid #20c20e;
  border-left: 0px;
  border-radius: 20px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  box-shadow: 0 0 10px 1px #20c20e;
  padding: 5px;
  margin: auto;
  margin-top: 15px;
  overflow: auto;
  direction: rtl;
}

.tech-stack-card {
  text-align: center;
  width: 100%;
  height: 100px;
  background-color: #031001;
  color: #20c20e;
  border: 1px solid #20c20e;
  box-shadow: 0px 0px 14px 4px #20c20e inset;
  border-radius: 15px;
  margin: 8px 0px 8px 0px;
}

.tech-stack-card:hover {
  background-color: #20c20e;
  color: #031001;
  border: 1px solid #031001;
  box-shadow: 0px 0px 12px 2px #031001 inset;
  cursor: pointer;
  width: 101%;
  transition: 0.3s ease-out;
}

.tech-icon {
  font-size: 35px;
  margin-bottom: 10px;
}

.tech-stack-media-body h5 {
  font-size: 11px;
  text-align: center;
}

@media only screen and (max-width: 1000px) {
  .tech-stack-header h2 {
    font-size: 22px;
  }

  .tech-stack-header p {
    font-size: 7px;
  }
}
@media only screen and (max-width: 800px) {
  .tech-stack-header {
    padding-left: 15;
  }
  .tech-stack-header h2 {
    font-size: 15px;
  }

  .tech-stack-header p {
    font-size: 6px;
  }
}

@media only screen and (max-width: 620px) {
  .tech-stack-header {
    padding-left: 9;
    /* justify-content: center; */
  }

  .tech-stack-header h2 {
    font-size: 10px;
  }

  .tech-stack-header p {
    font-size: 4px;
  }
}
