.edu-container {
  /* border: 2px solid #20c20e; */
  width: 100%;
  padding: 30px;
  margin-top: 25px;
  margin-bottom: 25px;
}

.edu-header h2 {
  font-weight: 26px;
}

/* .edu-body {
} */

.edu-cover {
  width: 100%;
  height: 100%;
  position: relative;
  border-radius: 100%;
  cursor: -webkit-zoom-in;
}

.edu-cover:hover {
  width: 205%;
  height: 205%;
  margin: -49px -47px;
  border: 1px solid #20c20e;
  box-shadow: 0px 0px 15px 10px #20c20e inset;
  transition: all 0.5s ease-in-out;
  position: fixed;
}

.vertical-timeline-element-icon button {
  background: #20c20e;
  color: #031001;
  font-size: 15px;
}
.vertical-timeline-element-icon button:hover {
  background-color: #031001;
  color: #20c20e;
  box-shadow: 0px 0px 5px 10px #20c20e;
}
.vertical-timeline-element-icon button:active {
  transform: translateX(10px);
  box-shadow: 10px 2px 30px 5px #20c20e;
}

.vertical-timeline-element-title {
  font-size: 16px;
  /* font-weight: bolder; */
  color: #5195e9;
  text-align: center;
}

.vertical-timeline-element-subtitle {
  font-size: 13px;
  /* font-weight: bolder; */
  margin-top: 15px;
  color: white;
  text-align: center;
}

.vertical-timeline-element-date {
  font-size: 16px;
  /* font-weight: bolder; */
  margin-left: 25px;
  color: rgb(255, 0, 0);
}

@media only screen and (max-width: 1170px) {
  .edu-body {
    padding: 10px;
    margin-left: 50px;
  }

  .edu-cover {
    transform: scaleX(-1);
  }
  .edu-cover:hover {
    width: 195%;
    height: 195%;
    margin: -42px -42px;
  }

  .vertical-timeline-element-work {
    margin-left: 20px;
  }

  .vertical-timeline-element-title {
    font-size: 15px;
  }

  .vertical-timeline-element-subtitle {
    font-size: 12px;
  }

  .vertical-timeline-element-date {
    margin: auto;
    margin-top: 10px;
    margin-bottom: -10px;
    right: 0;
    margin-left: 10%;
    text-align: right;
  }
}

@media only screen and (max-width: 600px) {
  .edu-body {
    margin-left: 18px;
  }
}
