.home-container {
  margin-top: 20px;
  height: 400px;
  /* background-color: #20c20e; */
}
.home-content {
  padding-top: 120px;
  padding-left: 30px;
  margin-right: 30px;
  position: absolute;
  /* overflow-y: scroll; */
}
.home-content h3 {
  font-size: 20px;
  padding-left: 4px;
}

.home-content h2 {
  padding-top: 10px;
  font-size: 21px;
  display: inline;
}

.home-content h1 {
  padding-top: 10px;
  padding-left: 4px;
  font-size: 20px;
}

.cover-photo {
  display: block;
  width: 60%;
  height: 450px;
  margin: auto;
  opacity: 0.11;
  padding-top: 30px;
}

.home-btns {
  padding-left: 30px;
  position: absolute;
  z-index: 2;
  margin-top: 300px;
  text-align: center;
}

.home-btns .hire-btn {
  width: 110px;
  height: 45px;
  text-align: center;
  font-size: 10px;
  background-color: transparent;
  color: #20c20e;
  border: 2px solid #20c20e;
  box-shadow: 2px 3px 8px 2px black;
  border-radius: 20px;
  margin-right: 5px;
}

.home-btns .hire-btn:hover {
  background-color: #20c20e;
  color: #031001;
  width: 130px;
  transition: all 0.12s ease-in-out;
}

.home-btns .hire-btn:active {
  background-color: #20c20e;
  color: #031001;
  box-shadow: 0px -6px 0px 0px black;
  transform: translateY(-7px);
}
.home-btns .resume-btn {
  width: 110px;
  height: 45px;
  text-align: center;
  font-size: 10px;
  background-color: transparent;
  color: #20c20e;
  border-radius: 20px;
  border: 2px solid #20c20e;
  box-shadow: 2px 3px 8px 2px black;
  margin-left: 5px;
}

.home-btns .resume-btn:hover {
  background-color: #20c20e;
  color: #031001;
  width: 130px;
  transition: all 0.12s ease-in-out;
}

.home-btns .resume-btn:active {
  background-color: #20c20e;
  color: #031001;
  box-shadow: 0px 6px 0px 0px black;
  transform: translateY(7px);
}

@media only screen and (max-width: 1000px) {
  .home-content {
    padding-left: 20px;
    padding-right: 170px;
  }
  .home-content h3 {
    font-size: 19px;
    padding-left: 3px;
  }

  .home-content h2 {
    padding-top: 5px;
    font-size: 20px;
  }

  .home-content h1 {
    padding-top: 5px;
    padding-left: 3px;
    font-size: 19px;
  }
  .home-btns {
    margin-top: 280px;
    padding-left: 20px;
  }
}
@media only screen and (max-width: 800px) {
  .cover-photo {
    width: 80%;
    height: 430px;
  }

  .home-content {
    padding-top: 120px;
    padding-left: 30px;
    padding-right: 160px;
  }
  .home-content h3 {
    font-size: 16px;
    padding-left: 3px;
  }

  .home-content h2 {
    padding-top: 5px;
    font-size: 17px;
  }

  .home-content h1 {
    padding-top: 5px;
    padding-left: 3px;
    font-size: 16px;
  }

  .home-btns {
    padding-left: 35px;
    margin-top: 280px;
  }
}

@media only screen and (max-width: 700px) {
  .cover-photo {
    width: 100%;
    height: 400px;
  }

  .home-content {
    padding-top: 100px;
    margin-left: -40px;
    padding-right: 180px;
  }
  .home-content h3 {
    font-size: 15px;
    padding-left: 2px;
  }

  .home-content h2 {
    padding-top: 5px;
    font-size: 16px;
  }

  .home-content h1 {
    padding-top: 5px;
    padding-left: 2px;
    font-size: 15px;
  }

  .home-btns {
    padding-left: 0px;
    /* margin-top: 100px; */
  }
}

/* For Mobile View */

@media only screen and (max-width: 600px) {
  .home-content {
    margin-top: -30px;
    margin-left: -50px;
  }

  .home-content h3 {
    font-size: 11.5px;
  }

  .home-content h2 {
    font-size: 12px;
  }

  .home-content h1 {
    font-size: 11px;
  }

  .home-btns {
    margin-left: -20px;
    margin-top: 260px;
    padding: -10px;
  }

  .home-btns .hire-btn {
    width: 105px;
    font-size: 9.5px;
  }

  .home-btns .hire-btn:hover {
    width: 120px;
  }

  .home-btns .resume-btn {
    width: 105px;
    font-size: 9.5px;
  }

  .home-btns .resume-btn:hover {
    width: 120px;
  }
}
