.contact-container {
  align-items: center;
  margin: 50px 40px;
}

.card0 {
  background-color: #031001;
  border-radius: 35px;
  box-shadow: 0px 0px 15px 2px #20c20e;
}

.card1 img {
  max-width: 100%;
  max-height: 60vh;
  border-radius: 35px;
  margin: auto;
  opacity: 0.6;
}

.card2 {
  width: 100%;
  height: 57vh;
  margin: auto;
  padding:10px 0px 0px 30px;
  font-size: 10px;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: #20c20e;
  border-radius: 30px;
}

.card2 input {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  padding: 10px;
  margin: auto;
}

.card2 textarea {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  padding: 10px;
}

.card2 button {
  border-radius: 20px;
  width: 40%;
  padding: 10px;
  margin: auto;
  background-color: #031001;
  color: #20c20e;
  font-size: 12px;
  box-shadow: 0px 4px 2px 0px #031001;
  border: 1px solid #20c20e;
}

.card2 button:hover {
  box-shadow: 0px 6px 4px 0px #031001;
}

.card2 button:active {
  transform: translateY(5px);
  box-shadow: 0px 10px 8px 1px #031001;
}

@media only screen and (max-width: 767px) {
  .card0 {
    display: flex;
    flex-direction: column;
  }

  .card1 img {
    width: 100%;
    border-bottom-left-radius: 0px;
  }

  .card2 {
    width: 100%;
    height: 100%;
  }

  .card2 button {
    width: 80%;
  }
}
