.top-projects {
  background-color: #20c20e;
  color: #031001;
  border: 2px solid #20c20e;
  border-radius: 20px;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  margin: 50px 10px;
  box-shadow: 0px 0px 10px 5px #031001;
}

.top-projects-header {
  padding: 20px;
  text-align: center;
  margin-bottom: -40px;
}

.top-projects-header h2 {
  font-size: 26px;
  margin-bottom: 20px;
}

/* top project card */

.projects-card {
  width: 100%;
  max-height: 100%;
  background-color: #031001;
  color: #20c20e;
  border-radius: 20px;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  margin: auto;
  margin-top: 50px;
  margin-bottom: 20px;
  box-shadow: 0px 0px 5px 2px #031001;
}
.card-image img {
  width: 100%;
}

.card-notify-badge {
  position: absolute;
  top: -52px;
  left: -4px;
  border: 2px solid #20c20e;
  border-radius: 20px;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  padding: 15px;
  background-color: #031001;
  color: #20c20e;
  box-shadow: 0px 0px 3px 1px #20c20e inset;
  font-size: 10px;
}

.card-detail-badge {
  padding: 0px 5px;
}

.card-image-overly {
  width: 100%;
  place-content: center;
  margin: auto;
  justify-content: space-between;
  font-size: 9px;
  padding: 6px 0px;
  border: 0px solid #20c20e;
  box-shadow: 0px 0px 5px 1px #20c20e inset;
}

.projects-card-body {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
  margin: -16px;
}

.projects-card-body h5 {
  font-size: 10px;
  text-align: center;
  margin-top: 15px;
}

.projects-card-body a {
  text-align: center;
  text-decoration-line: none;
  color: #031001;
  background-color: #20c20e;
  border-radius: 20px;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  padding: 15px;
  font-size: 15px;
  box-shadow: 0px 0px 10px 5px #031001 inset;
  margin-top: 20px;
}

.projects-card-body a:active {
  transform: translateY(5px);
  box-shadow: 0px 15px 20px 10px #031001;
}
.projects-card-body a:hover {
  color: #20c20e;
  background-color: #031001;
  box-shadow: 0px 5px 10px 0px #20c20e inset;
}

.view-all-project-btn {
  margin: 0px 0px;
}
.view-all-project-btn button {
  width: 100%;
  border-radius: 20px;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  padding: 20px;
  background-color: #031001;
  color: #20c20e;
  border: 2px solid #20c20e;
  font-size: 14px;
  /* box-shadow: 0px 0px 4px 0px #20c20e inset; */
}

.view-all-project-btn button:hover {
  box-shadow: 0px 0px 10px 1px #20c20e inset;
  transition: 0.1s ease-out;
}

.view-all-project-btn button:active {
  transform: translateY(5px);
  box-shadow: 0px 2px 20px 5px #20c20e;
  border: 5px solid #20c20e;
}
