.menu-nav-items {
  display: grid;
  grid-template-columns: repeat(5, 5fr);
  text-align: center;
  border: 0px solid #20c20e;
  border-radius: 10px;
  box-shadow: 0px 0px 0px #20c20e;
  width: 100px;
  position: absolute;
  z-index: 1;
  left: 0;
  margin-top: 28px;
  margin-left: 30px;
}
.menu-nav-link {
  padding: 20px;
}
.menu-nav-border1 {
  border: 1px solid #20c20e;
  border-bottom: 0px;
  border-right: 0px;
  border-top-left-radius: 20px;
  box-shadow: 0 0 10px 1px #20c20e inset;
}
.menu-nav-border2 {
  border: 1px solid #20c20e;
  border-left: 0px;
  border-right: 0px;
  border-radius: 0px;
  box-shadow: 0 0 10px 1px #20c20e inset;
}
.menu-nav-border3 {
  border: 1px solid #20c20e;
  border-left: 0px;
  border-right: 0px;
  border-radius: 0px;
  box-shadow: 0 0 10px 1px #20c20e inset;
}
.menu-nav-border4 {
  border: 1px solid #20c20e;
  border-left: 0px;
  border-right: 0px;
  border-radius: 0px;
  box-shadow: 0 0 10px 1px #20c20e inset;
}
.menu-nav-border5 {
  border: 1px solid #20c20e;
  border-left: 0px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  box-shadow: 0 0 10px 1px #20c20e inset;
}
.menu-nav-border6 {
  border: 1px solid #20c20e;
  border-top: 0px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  box-shadow: 0 0 10px 1px #20c20e inset;
}

.menu-nav-link button {
  background-color: #031001;
  color: #20c20e;
  display: inline-block;
  height: 45px;
  width: 45px;
  overflow: hidden;
  font-size: 8.3px;
  border: 1px solid #031001;
  border-radius: 50px;
  box-shadow: 0px 0px 8px 3px #20c20e;
  transition: all 0.3s ease-out;
}
.menu-nav-link button:hover {
  width: 145px;
}

.menu-nav-link button svg {
  display: inline-block;
  height: 45px;
  width: 45px;
  /* text-align: center; */
  font-size: 8px;
  border-radius: 50px;
  box-sizing: border-box;
  line-height: 60px;
  padding: 9px;
  margin-top: -2px;
  margin-left: -7px;
}
.menu-nav-link .btn-home:active {
  transform: translateY(4px);
}
.menu-nav-link .btn-home:hover {
  color: white;
  background-color: #20c20e;
  box-shadow: 1px 3px 4px white;
}

