/* All Responsive design */

@media only screen and (max-width: 700px) {
  .menu-nav-items {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    width: auto;
    border-radius: 20px;
    left: 0;
    margin-left: 5px;
    margin-top: 0px;
    position: absolute;
    z-index: 3;
  }

  .menu-nav-link {
    padding: 10px;
  }
  .menu-nav-border1 {
    border-top-right-radius: 20px;
    box-shadow: 0 0 6px 1px #20c20e inset;
  }
  .menu-nav-border2 {
    box-shadow: 0 0 6px 1px #20c20e inset;
  }
  .menu-nav-border3 {
    box-shadow: 0 0 6px 1px #20c20e inset;
  }
  .menu-nav-border4 {
    box-shadow: 0 0 6px 1px #20c20e inset;
  }
  .menu-nav-border5 {
    border: 1px solid #20c20e;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    box-shadow: 0 0 6px 1px #20c20e inset;
  }
  .menu-nav-border6 {
    box-shadow: 0 0 6px 1px #20c20e inset;
  }
}

@media only screen and (max-width: 600px) {
  .menu-nav-items {
    margin-left: 5px;
  }
}
