.btn {
  width: 50px;
  height: 50px;
  color: #20c20e;
  margin: 15px;
  margin-top: 70px;
  border: 1px solid;
  border-radius: 100px;
  box-shadow: 0px 0px 50px 20px #031001;  
  transition: all 0.2s ease-in-out;
}

.btn:active {
  transform: translateX(3px);
}
.btn:hover {
  background-color: #20c20e;
  box-shadow: 0px 0px 10px 5px #20c20e;
  color: #031001;
}

@media only screen and (max-width: 700px) {
  .btn {
    margin-top: 500px;
    transform: scaleX(-1);
  }

  .btn:active {
    transform: scaleX(-1);
  }

  .leftbar-toggle-icons .btn {
    transform: rotate(30deg);
  }
}

@media only screen and (max-width: 600px) {
  .btn:active {
    transform: scaleX(-1);
  }

  .leftbar-toggle-icons .btn {
    transform: rotate(30deg);
  }
}
