.about {
  /* border: 2px solid #031001; */
  box-shadow: 0px 0px 8px 2px #031001 inset;
  background-color: #20c20e;
  border-radius: 20px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  margin: 50px 0px 25px 40px;
  overflow: auto;
  height: 600px;
}

.row {
  padding: 20px;
  /* color: #20c20e; */
  color: #031001;
  width: 100%;
  padding-right: 0px;
  padding-bottom: 0px;
}

.about-img img {
  max-width: 40%;
  float: left;
  margin-right: 18px;
  margin-bottom: 4px;
  border: 2px solid #031001;
  box-shadow: 0px 0px 10px 2px #031001;
  border-radius: 20px;
}

.row h1 {
  text-align: center;
  font-size: 20px;
  margin-bottom: 20px;
}

.row p {
  text-align: justify;
  font-size: 11px;
}
